<template>
  <div class="product-block">
    <div class="title">
      <van-checkbox v-model="parentData.moreChoicesCheckedAll" @click="checkedAll(!parentData.moreChoicesCheckedAll)">全套</van-checkbox>
    </div>

    <ul>
      <li v-for="(item, index) in parentData.scenesProductsData" :key = 'index'>
        <van-checkbox v-model="item.checked" @change="checkedChange"></van-checkbox>
        <img :src="item.imageUrl | imageToHttps" alt="" @click="viewDetail(item.id)"/>
        <div class="product" @click="viewDetail(item.id)">
          <div class="product-title">{{item.name}}</div>
          <div class="introduce">{{item.title}}</div>
          <div class="price">
            <span>
              ￥{{item.price|fmtMoneyFilter}}
            </span>
            <span>x1</span>
          </div>
        </div>
      </li>
    </ul>

    <div class="total">
      <div class="price">
        总计: <font>￥{{parentData.priceTotal|fmtMoneyFilter}}</font>
      </div>
      <div class="submit" @click="isLogin">
        一键下单
      </div>
    </div>

  </div>
</template>

<script>
/**
 * tost提醒
 */
import Vue from 'vue';
import { Checkbox } from 'vant';

/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';
Vue.use({ Checkbox });

/*
 * 智慧场景-产品列表
*/
export default {
  name: 'product',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
    parentCustomizeAlert: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {
    }
  },
  mounted() {
  },
  methods: {

    /**
     * @description 判断登陆状态，未登录便登陆
     */
    isLogin() {
      const context = this;
      // 判断登陆状态，未登录便登陆
      if (context.$util.isHaierApp()) { // 智家
        context.$store.dispatch('handleUpAuth').then((res) => {
          console.log(res); // return true / false
          if (res) {
            // 创建订单
            context.createOrder();
          } else {
            context.parentCustomizeAlert.title = '用户信息异常';
            context.parentCustomizeAlert.type = 'fail';
            context.parentCustomizeAlert.dialog = true;
          }
        });
      } else {
        // h5
        // 创建订单
        context.createOrder();
      }
    },

    /**
     * @description 创建订单 商品数量为0时 不能提交订单
    */
    createOrder() {
      const context = this;
      const list = context.parentData.scenesProductsData;
      let dataProList = [];
      list.map(item => {
        if (item.checked) {
          item.num = 1;
          item.proId = item.id;
          dataProList.push(item)
        }
      })
      const data = {
        ac: 1,
        ch: 61,
        proList: dataProList,
        street: context.parentData.streetId,
        v: 2,
      };
      if (data.proList.length === 0) {
        context.parentCustomizeAlert.title = '请至少选择1种商品';
        context.parentCustomizeAlert.type = 'fail';
        context.parentCustomizeAlert.dialog = true;
        return
      }
      context.parentCustomizeAlert.title = '订单创建中';
      context.parentCustomizeAlert.type = 'loading';
      context.parentCustomizeAlert.dialog = true;
      personalApi.createOrder(data).then(res => {
        const _resData = res;
        if (_resData.success) {
          context.parentCustomizeAlert.title = '订单创建成功';
          context.parentCustomizeAlert.type = 'success';
          context.parentCustomizeAlert.dialog = true;
          setTimeout(() => {
            context.parentCustomizeAlert.dialog = false;
            // 成功后跳转到填写订单页面
            context.$router.push({
              path: '/orderCreate'
            })
          }, 1000)
        } else {
          context.parentCustomizeAlert.title = _resData.message;
          context.parentCustomizeAlert.type = 'fail';
          context.parentCustomizeAlert.dialog = true;
        }
      })
    },

    /**
     * @description 跳转到查看详情页面
     * @param {}
    */
    viewDetail (productId) {
      const context = this;
      context.$router.push({
        path: `/goodsDetail?productId=${productId}`,
      });
    },

    /**
     * @description 全选/反选
     * @param {isCheckedAll} true全选 false不全选
     * @param {priceTotal} 商品金额合计
     * @param {countTotal} 商品数量合计
    */
    checkedAll(value) {
      const context = this;
      const list = context.parentData.scenesProductsData;
      let isCheckedAll = value;
      let priceTotal = 0;
      let countTotal = 0;
      list.map((item, index) => {
        item.checked = isCheckedAll;
        if (isCheckedAll) {
          priceTotal = parseFloat(priceTotal) + parseFloat(item.price);
          countTotal = parseFloat(countTotal) + 1;
        }
      })
      context.parentData.priceTotal = priceTotal;
      context.parentData.countTotal = countTotal;
    },

    /**
     * @description 清单列表中的清单选择、 合计商品总价、已选商品数量、 判断是否需要全选或者反选
     * @param {isCheckedAll} true全选 false不全选
     * @param {priceTotal} 商品金额合计
     * @param {countTotal} 商品数量合计
    */
    checkedChange() {
      const context = this;
      const list = context.parentData.scenesProductsData;
      let priceTotal = 0;
      let countTotal = 0;
      let isCheckedAll = true;
      list.map((item, index) => {
        if (item.checked === false) {
          isCheckedAll = false;
        }
        if (item.checked) {
          priceTotal = parseFloat(priceTotal) + parseFloat(item.price);
          countTotal = parseFloat(countTotal) + 1;
        }
      })
      context.parentData.moreChoicesCheckedAll = isCheckedAll;
      context.parentData.priceTotal = priceTotal;
      context.parentData.countTotal = countTotal;
    },
  },
}
</script>

