<template>
  <div class="content" :style="styleBackground">

    <van-icon name="clear" @click="goHistory"/>

    <detail v-if="data.scenesProductsData" :parentData = "data"></detail>

    <div class="footer">
      <img @click="commodityListToggle" src="http://cdn09.ehaier.com/shunguang/H5/www/img/linshi/qdh@2x.png" alt=""/>
      <img @click="sceneIntroductionToggle" src="http://cdn09.ehaier.com/shunguang/H5/www/img/linshi/cjjs@2x.png" alt=""/>
    </div>

    <!--场景介绍-->
    <van-popup v-model="data.sceneIntroductionDialog" position="bottom" closeable class="scene-introduction-dialog" :style="{ height: '33%' }" overlay-class="popup-overlay-light">
      <div class="text" v-if="data.scenesImageDataChecked">
        {{data.scenesImageDataChecked.introduction}}
      </div>
    </van-popup>

    <!--商品列表-->
    <van-popup v-model="data.commodityListDialog" position="right" closeable class="commodity-list-dialog" :style="{ height: '100%' }" overlay-class="popup-overlay-light">
      <product v-if="data.scenesProductsData" :parentCustomizeAlert="customizeAlert" :parentData = "data"></product>
    </van-popup>

    <!--自定义提醒弹窗-->
    <customize-alert v-if="customizeAlert.dialog" :parentData = "customizeAlert"></customize-alert>

  </div>
</template>

<script>
/*
 * 方案介绍
*/
import detail from './detail';

/*
 * 金额清单列表
*/
import product from './product';

/*
 * 自定义提醒弹窗
*/
import customizeAlert from './customize-alert';

/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';

/*
 * 智慧场景
*/
export default {
  name: 'itSoluteList',
  components: {
    detail,
    product,
    customizeAlert
  },
  data() {
    return {
      /**
       * {windowWidth} 手机横屏后的宽度（竖屏时的高度） 宽度 > 高度
       * {windowHeight} 手机横屏后的高度（竖屏时的宽度）
       * {channel} 渠道 1:app 2:pc
       * {scenesId} 场景id
       * {spaceId} 空间Id
       * {provinceId} 省id
       * {cityId} 市id
       * {regionId} 区县id
       * {streetId} 街道id
       * {productsIds} 商品Ids
       * {memberId} 登录人的memberId
       * {countTotal} 商品总数
       * {priceTotal} 商品总价
       * {moreChoicesCheckedAll} true全选  false不全选
       * {scenesImageData} 场景背景图data
       * {scenesImageDataChecked} 当前选中的背景图场景信息data
       * {scenesCoordinateData} 智能场景定位信息
       * {scenesProductsData} 智能场景图商品信息
       * {scenesProductsDataChecked} 当前选中的智能场景图商品信息
       * {commodityListDialog} 商品列表 展示/隐藏
       * {sceneIntroductionDialog} 场景介绍 展示/隐藏
       * {styleBackground} 定义当前页面的样式布局
       * {customizeAlert} 自定义提醒  type： loading加载中  success成功  fail失败
       */
      data: {
        windowWidth: 0,
        windowHeight: 0,
        channel: 1,
        scenesId: null,
        spaceId: null,
        provinceId: '',
        cityId: '',
        regionId: '',
        streetId: '',
        productsIds: '',
        memberId: '',
        countTotal: 0,
        priceTotal: 0,
        moreChoicesCheckedAll: true,
        scenesImageData: null,
        scenesImageDataChecked: null,
        scenesCoordinateData: null,
        scenesProductsData: null,
        scenesProductsDataChecked: null,
        commodityListDialog: false,
        sceneIntroductionDialog: false,
      },
      styleBackground: {
        width: 0,
        height: 0,
        top: 0,
        left: 0
      },
      customizeAlert: {
        title: '加载中',
        type: 'loading',
        dialog: false,
        time: null,
      },
      isPlus: false, // 是否是plus会员
    }
  },
  created() {
    const context = this;
    const _query = context.$route.query;
    context.data.scenesId = _query.scenesId;

    context.data.provinceId = context.$store.state.locationInfo.provinceId || 16; // 省id
    context.data.cityId = context.$store.state.locationInfo.cityId || 173; // 市id
    context.data.regionId = context.$store.state.locationInfo.districtId || 2450; // 区id
    context.data.streetId = context.$store.state.locationInfo.townshipId || 12036596; // 街道id

    context.data.memberId = context.$store.state.mId; // 登录人memberId
    context.data.windowWidth = window.screen.height; // 横屏后的高度就是宽度
    context.data.windowHeight = window.screen.width; // 横屏后的宽度就是高度

    // 定义当前页面的样式布局
    context.styleBackground.width = `${context.data.windowWidth}px`;
    context.styleBackground.height = `${context.data.windowHeight}px`;
    context.styleBackground.top = `${(context.data.windowWidth - context.data.windowHeight) / 2}px`;
    context.styleBackground.left = `-${(context.data.windowWidth - context.data.windowHeight) / 2}px`;
  },
  mounted() {
    const context = this;
    context.customizeAlert = {
      title: '加载中',
      type: 'loading',
      dialog: true,
      time: null,
    };
    // 获取定位
    // context.$store.dispatch('doLocation').then((res) => {
    //   if (res === 400) {
    //     context.customizeAlert = {
    //       title: '定位异常，请刷新重试',
    //       type: 'fail',
    //       dialog: true,
    //     };
    //   } else {
    //     context.$store.dispatch('doLocationInfo', res).then(result => {
    //       if (result === 400) {
    //         context.customizeAlert = {
    //           title: '定位异常，请刷新重试',
    //           type: 'fail',
    //           dialog: true,
    //         };
    //       } else {
    //         context.data.provinceId = result.provinceId; // 省id
    //         context.data.cityId = result.cityId; // 市id
    //         context.data.regionId = result.districtId; // 区id
    //         context.data.streetId = result.townshipId; // 街道id
    /**
     * @description 获取智能场景的背景图片
    */
    context.getScenesImage();
    //       }
    //     })
    //   }
    // })
  },
  methods: {

    /**
     * @description 获取智能场景的背景图片
     * @param {channel} 渠道1:app 2:pc
     * @param {scenesId} 场景id
    */
    getScenesImage () {
      const context = this;
      // context.customizeAlert = {
      //   title: '加载中',
      //   type: 'loading',
      //   dialog: true,
      //   time: null,
      // };
      const data = {
        channel: context.data.channel,
        scenesId: context.data.scenesId
      }
      personalApi.scenesImage(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          context.customizeAlert.dialog = false;
          context.data.scenesImageData = _resData.data;
          context.data.scenesImageDataChecked = _resData.data.productDetailVOS[0].spaces[0];
          context.data.spaceId = context.data.scenesImageDataChecked.id;
        } else {
          context.customizeAlert = {
            title: _resData.message,
            type: 'fail',
            dialog: true,
            time: null,
          };
        }
      }).then(res => {
        /**
         * @description 获取智能场景定位信息
         * @param {spaceId} 当前场景的空间id
        */
        context.getScenesCoordinate();
      })
    },

    /**
     * @description 获取智能场景定位信息
     * @param {spaceId} 当前场景的空间id
    */
    getScenesCoordinate () {
      const context = this;
      const data = {
        spaceId: context.data.spaceId,
      }
      personalApi.scenesCoordinate(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          context.data.scenesCoordinateData = _resData.data;
          context.data.productsIds = _resData.data.productsIds;
        } else {
          context.customizeAlert = {
            title: _resData.message,
            type: 'fail',
            dialog: true,
            time: null,
          };
        }
      }).then(res => {
        /**
         * @description 获取智能场景图商品信息
         * @param {channel} 渠道1:app 2:pc
         * @param {scenesId} 场景id
        */
        context.getScenesProducts();
      })
    },

    /**
     * @description 获取智能场景图商品信息
     * @param {channel} 渠道1:app 2:pc
     * @param {scenesId} 场景id
    */
    getScenesProducts () {
      const context = this;
      const data = {
        productsIds: context.data.productsIds,
        streetId: context.data.streetId,
        memberId: context.data.memberId,
        cityId: context.data.cityId,
        provinceId: context.data.provinceId,
        regionId: context.data.regionId,
      }
      personalApi.scenesProducts(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          // 给每个商品添加checked选项，用于checkbox
          _resData.data.map((item, index) => {
            item.checked = true;
          })
          this.isPlus = _resData.data.plus;
          context.data.scenesProductsData = _resData.data.productDetailVOS;
          context.data.scenesProductsDataChecked = _resData.data.productDetailVOS[0];
        } else {
          context.customizeAlert = {
            title: _resData.message,
            type: 'fail',
            dialog: true,
            time: null,
          };
        }
      })
    },

    /**
     * @description 返回上一页
     * @param {}
    */
    goHistory () {
      const context = this;
      // 返回上一层
      context.$store.dispatch('doHistoryBack');
      // context.$router.go(-1);
    },

    /**
     * @description 商品列表 展示/隐藏
     * @param {}
    */
    commodityListToggle () {
      const context = this;
      context.data.commodityListDialog = true;
    },

    /**
     * @description 场景介绍 展示/隐藏
     * @param {}
    */
    sceneIntroductionToggle () {
      const context = this;
      context.data.sceneIntroductionDialog = true;
    },

  },
}
</script>

<style lang="scss" scoped>
.content {
  transform: rotate(90deg);
  transform-origin: 50% 50%;
  position: absolute;

  .van-icon-clear {
    position: absolute;
    right: 0.2rem;
    top: 0.2rem;
    font-size: 0.5rem;
    color: #888888;
    z-index: 1;
  }

  .footer {
    position: absolute;
    left: 0.4rem;
    bottom: 0.4rem;
    display: flex;

    img {
      display: block;
      width: 0.8rem;
      height: 0.8rem;
      margin: 0 0.2rem 0 0;
    }

  }

  /**
  * popup样式
  */
  ::v-deep.scene-introduction-dialog {
    background: rgba(0,0,0,.5);
    color: #ffffff;
    border-top-right-radius: 0.2rem;
    border-top-left-radius: 0.2rem;

    .van-popup__close-icon {
      color: #ffffff;
    }

    .text {
      padding: 0.3rem;
    }

  }

  ::v-deep.popup-overlay-light {
    background: rgba(0,0,0,.3);
  }

}

</style>
