<template>
  <div class="loading-block">
    <van-loading
      v-if="parentData.type === 'loading'"
      type=""
    />
    <van-icon
      v-if="parentData.type === 'fail'"
      size="36px"
      name="fail"
    />
    <van-icon
      v-if="parentData.type === 'success'"
      size="36px"
      name="success"
    />
    <div class="title">
      {{parentData.title}}
    </div>
  </div>
</template>

<script>
/**
 * tost提醒
 */
import Vue from 'vue'
import { Loading } from 'vant'
Vue.use({ Loading })

/*
 * 智慧场景-loading
 */
export default {
  name: 'loading',
  props: {
    /**
     * 父组件传入数据源
     * customizeAlert: {
        title: '加载中', // title提示信息
        type: 'loading', // loading加载中  success成功  fail失败
        dialog: true, // true展示 false隐藏
        time: null, // 默认3000毫秒,如果是loading的话，没有设置time的话，默认是永久，除非dialog=false手动关闭
      },
     */
    parentData: {
      type: null,
      default: function() {
        return {}
      },
    },
  },
  data() {
    return {}
  },
  mounted() {
    /**
     * 如果没有设置time的话，默认3000毫秒后关闭
     */
    const context = this
    console.log('context.parentData', context.parentData)
    if (!context.parentData.time) {
      const _time = context.parentData.type === 'loading' ? 999999 : 3000
      console.log('_time', _time)
      setTimeout(() => {
        context.parentData.dialog = false
      }, _time)
    } else {
      setTimeout(() => {
        context.parentData.dialog = false
      }, context.parentData.time)
    }
  },
  methods: {},
}
</script>

