<template>
  <div class="detail-block">
    <img :src="parentData.scenesImageDataChecked.imageUrl | imageToHttps" alt=""/>
    <div class="info">
      <!--场景切换-->
      <div class="scene-select">
        <ul>
          <li @click="sceneSelect(item)" :class="{'checked': parentData.scenesImageDataChecked.id === item.id}" v-for="(item,index) in parentData.scenesImageData[0].spaces" :key="index">
            {{item.name}}
          </li>
        </ul>
      </div>

      <div class="product-space"
        :style=" item.direction === 1 ?
          {
          'left': `${item.x * 100 - (5 / parentData.windowWidth * 100)}%`,
          'top' : `${item.y * 100 - (35 / parentData.windowHeight * 100)}%`
          } :
          {
            'left': `${item.x * 100 + (5 / parentData.windowWidth * 100)}%`,
            'top': `${item.y * 100 - (35 / parentData.windowHeight * 100)}%`
          }"
        v-for="(item,index) in parentData.scenesCoordinateData.labels"
        :key="index"
        @click="productDialogToggle(item)"
      >
        <van-tag mark type="primary">{{item.name}}</van-tag>
      </div>

    </div>

    <!--查看产品详情的弹窗-->
    <van-popup v-model="productDialog" closeable>
      <div class="product-dialog" @click="toProductDetail(parentData.scenesProductsDataChecked.id)">
        <img :src="parentData.scenesProductsDataChecked.imageUrl | imageToHttps" alt=""/>
        <div class="introduce">
          <h3>
            {{parentData.scenesProductsDataChecked.name}}
          </h3>
          <div class="price">
            ￥{{parentData.scenesProductsDataChecked.price|fmtMoneyFilter}}
          </div>
        </div>
      </div>
    </van-popup>

  </div>
</template>

<script>
/**
 * vant
 */
import Vue from 'vue';
import { Tag, Popup } from 'vant';

Vue.use({ Tag, Popup });

/*
 * 智慧场景-详情
*/
export default {
  name: 'detail',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {
      /**
       * {productDialog} 产品详情弹窗 展示/隐藏
       */
      productDialog: false,
    }
  },
  mounted() {
  },
  methods: {
    /**
     * @description 场景切换
     * @param {}
     * 返回接口请求数据
    */
    sceneSelect (data) {
      const context = this;
      context.parentData.scenesImageDataChecked = data;
      // 重新调用获取空间 和 产品信息的方法
      context.parentData.spaceId = data.id;
      context.$parent.getScenesCoordinate();
    },

    /**
     * @description 产品信息 展示/隐藏
     * @param {}
     * 返回接口请求数据
    */
    productDialogToggle (data) {
      const context = this;
      context.parentData.scenesProductsData.map((item, index) => {
        if (data.productId === item.id) {
          context.parentData.scenesProductsDataChecked = item;
        }
      })
      context.productDialog = true;
    },

    /**
     * @description 跳转到查看产品详情页面
     * @param {}
     * 返回接口请求数据
    */
    toProductDetail (productId) {
      const context = this;
      context.$router.push({
        path: `/goodsDetail?productId=${productId}`,
      });
    },

  },
}
</script>

